.register-container {
  width: 900px;
  max-width: 90%;
  margin: 30px auto;
  background-color: #ffffff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.btnReg {
  width: 100%;
  margin-top: 40px;
}

form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.roww {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

.roww input[type="text"],
.roww input[type="email"],
.roww input[type="tel"],
.roww input[type="password"],
.roww input[type="date"],
.roww select {
  flex: 1;
  width: 90%;
  padding: 10px;
  font-size: 14px;
  background-color: whitesmoke;
  border: none;
  border-bottom: 1px solid #fff;
  color: black;
}

.login-text {
  margin-top: 20px;
  text-align: center;
  font-size: 14px;
}