.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(30, 30, 30, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
  }
  
  .modal {
    background: #fff;
    padding: 2rem;
    border-radius: 10px;
    width: 400px;
    position: relative;
    text-align: center;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
  }
  
  .close-btn {
    position: absolute;
    top: 15px;
    right: 20px;
    font-size: 20px;
    background: #f4c04b;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
  
  .title {
    font-size: 24px;
    margin-bottom: 1rem;
  }
  
  .options {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 1rem;
    color: #333;
  }
  .options label{
    color: #000;
  }
  .input {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: none;
    border-bottom: 1px solid #333;
    font-size: 16px;
    color: #000;
  }
  
  .forgot {
    text-align: right;
    font-size: 14px;
    color: #333;
    margin-bottom: 1rem;
    cursor: pointer;
  }
  
  .login-btn,
  .otp-btn {
    width: 100%;
    padding: 0.6rem;
    background-color: #000;
    color: #fff;
    border: none;
    margin-bottom: 1rem;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .or {
    margin: 0.5rem 0;
    font-weight: bold;
  }
  
  .create-account {
    color: #000;
    font-weight: 500;
    cursor: pointer;
  }
  